input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

.intro-logo {
  float: right; }

.smiley {
  font-size: 90px;
  cursor: pointer; }

.happy {
  color: #FBFFFE; }

.sad {
  color: #F4AC45; }

.home {
  min-height: 100vh;
  display: flex;
  position: relative; }
  @media only screen and (max-width: 992px) {
    .home {
      display: none; } }
  .home .home-large {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    -webkit-transition: 0.5s all;
    transition: 0.5s all;
    -webkit-filter: brightness(0.9);
            filter: brightness(0.9); }
    .home .home-large:hover {
      -webkit-filter: brightness(1);
              filter: brightness(1); }
    .home .home-large .home-large-msg {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      height: 300px;
      position: relative; }
      .home .home-large .home-large-msg .home-large-title {
        font-size: 90px;
        cursor: pointer;
        padding-bottom: 40px; }
  .home .home-large.stay {
    background: #F4AC45;
    padding-right: 200px; }
  .home .home-large.clear-stay {
    padding: 0;
    background: #F4AC45; }
  .home .home-large.go {
    background: #FBFFFE;
    padding-left: 200px; }
  .home .home-large.clear-go {
    padding: 0;
    background: #FBFFFE; }
  .home .home-large-title.go {
    color: #F4AC45; }
  .home .home-large-title.stay {
    color: #FBFFFE; }
  .home .anxious-msg {
    position: absolute;
    bottom: 20%; }

.home-small {
  min-height: 100vh;
  position: relative;
  background: #0277bd;
  display: flex;
  justify-content: space-evenly;
  align-items: center; }
  @media only screen and (min-width: 601px) {
    .home-small {
      display: none; } }
  .home-small .home-small-title {
    position: absolute;
    top: 5%; }
  .home-small h1, .home-small h2 {
    font-family: 'Satisfy', cursive;
    color: #FBFFFE; }
  .home-small .small-sad-msg {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: inherit;
    align-items: center; }
    @media only screen and (max-width: 600px) {
      .home-small .small-sad-msg {
        width: 200px;
        text-align: center; } }

